* {
  list-style-type: none;
  font-family: Arial, Helvetica, sans-serif;
  text-decoration: none;
}

.list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.card {
  width: 160px;
  height: 260px;
  text-align: center;
  padding: 16px;
  margin: 20px 40px;
  border: 1px solid grey;
  border-radius: 5px;
}

.types-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.types-items {
  display: inline-block;
}
